  font-family: "Golos-Text";
  src: local("Golos-Text"), url("../fonts/Golos-Text/Golos-Text_Regular.woff2") format("woff2"), url("../fonts/Golos-Text/Golos-Text_Regular.woff") format("woff"), url("../fonts/Golos-Text/Golos-Text_Regular.ttf") format("ttf"), url("/fonts/Golos-Text/Golos-Text_Regular.eot") format("eot");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "Golos-Text";
  src: local("Golos-Text"), url("../fonts/Golos-Text/Golos-Text_DemiBold.woff2") format("woff2"), url("../fonts/Golos-Text/Golos-Text_DemiBold.woff") format("woff"), url("../fonts/Golos-Text/Golos-Text_DemiBold.ttf") format("ttf"), url("/fonts/Golos-Text/Golos-Text_DemiBold.eot") format("eot");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-family: "Golos-Text";
  src: local("Golos-Text"), url("../fonts/Golos-Text/Golos-Text_Bold.woff2") format("woff2"), url("../fonts/Golos-Text/Golos-Text_Bold.woff") format("woff"), url("../fonts/Golos-Text/Golos-Text_Bold.ttf") format("ttf"), url("/fonts/Golos-Text/Golos-Text_Bold.eot") format("eot");
  font-weight: 700;
