@font-face {
  font-family: "Alegreya-Sans";
  src: local("Alegreya-Sans"), url("../fonts/Alegreya-Sans/AlegreyaSans-Bold.woff2") format("woff2"), url("../fonts/Alegreya-Sans/AlegreyaSans-Bold.woff") format("woff"), url("../fonts/Alegreya-Sans/AlegreyaSans-Bold.ttf") format("ttf"), url("/fonts/Alegreya-Sans/AlegreyaSans-Bold.eot") format("eot");
  font-weight: 700;
  font-style: normal;
}
body {
  margin: 0;
  background-color: #fff;
  font-family: "Golos-Text", sans-serif;
  font-size: 15px;
  line-height: 1.48;
  font-weight: 400;
  color: #404040;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

p {
  margin: 0 0 10px;
}

a {
  text-decoration: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

input, textarea, select, button {
  font-family: inherit;
}

.img {
  display: block;
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.text-uppercase {
  text-transform: uppercase;
}

.page {
  overflow: hidden;
}

/* Focus-element
================*/
:focus {
  outline: 2px solid #000;
}

:focus:not(:focus-visible) {
  outline: 0;
}

:focus-visible {
  outline: 2px solid #000;
}

/* Container
================*/
.container {
  width: 100%;
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}

/* Components */
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 82px;
}
.header__logo {
  display: flex;
  width: 100%;
  max-width: 520px;
  margin-right: 24px;
}
.header__logo-img {
  margin-right: 24px;
}
.header__logo-text {
  font-size: 11px;
  line-height: 1.58;
  color: #8F9090;
}

.logo {
  height: 32px;
}

.actions-menu {
  display: flex;
  align-items: center;
}
.actions-menu__list {
  display: flex;
}
.actions-menu__link {
  margin-right: 32px;
  font-size: 14px;
  line-height: 1.58;
  color: #003154;
}
.actions-menu__link--feedback {
  padding-left: 23px;
  position: relative;
}
.actions-menu__link--feedback:before {
  content: "";
  display: block;
  width: 17px;
  height: 20px;
  margin-top: -1px;
  background-image: url("../images/letter-icon.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 0;
  left: 0;
}
.actions-menu__search {
  padding: 0;
  border: 0;
  background: none;
}
.actions-menu__search:before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background-image: url("../images/search-icon.svg");
  background-repeat: no-repeat;
  background-size: contain;
}

.site-nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 54px;
}
.site-nav__link {
  padding-bottom: 7px;
  padding-top: 7px;
  font-size: 15px;
}
.site-nav__link--subnav {
  padding-right: 15px;
  position: relative;
}
.site-nav__link--subnav:after {
  content: "";
  display: block;
  width: 8px;
  height: 4px;
  margin-top: 1px;
  background-image: url("../images/arrow-icon.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
  top: 50%;
  right: 0;
  z-index: 1;
}

.intro {
  background: linear-gradient(90deg, rgb(247, 247, 248) 70.4%, rgb(96, 192, 195) 70.4%);
}
.intro__slider {
  overflow: visible;
}
.intro__slide {
  display: flex;
  align-items: center;
  height: 600px;
}
.intro__content {
  width: 50%;
  max-width: 486px;
  margin-right: auto;
}
.intro__decorative {
  display: flex;
  margin-bottom: 12px;
}
.intro__decorative-item:not(:last-child) {
  margin-right: 16px;
}
.intro__decorative-icon {
  width: 40px;
  height: 40px;
}
.intro__title {
  margin-bottom: 15px;
  font-weight: 600;
  font-size: 33px;
  line-height: 1.16;
  color: #003154;
}
.intro__description {
  margin-bottom: 54px;
  font-size: 18px;
  line-height: 1.62;
}
.intro__img {
  width: 50%;
}
.intro__slide-img {
  width: 588px;
  height: 440px;
  margin-left: auto;
  object-fit: cover;
  filter: drop-shadow(4px 8px 24px rgba(0, 0, 0, 0.2)) drop-shadow(16px 28px 64px rgba(0, 0, 0, 0.16));
}
.intro__swiper-pagination.swiper-pagination {
  bottom: 32px;
}
.intro__swiper-pagination > .swiper-pagination-bullet {
  background: #003154;
}

.btn {
  display: inline-block;
  vertical-align: middle;
  padding: 11px 58px;
  font-family: inherit;
  font-size: 15px;
  line-height: 1.48;
  color: #003154;
  font-weight: 400;
  text-align: center;
  text-decoration: none;
  background-color: transparent;
  border: 1px solid #003154;
  border-radius: 2px;
  cursor: pointer;
}